import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { useQMenuGuideDownload } from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

const MenuGuideDownloadButton = styled(TouchButton)`
  white-space: nowrap;
  margin-left: 2rem;
  & a {
    text-decoration: none;
    color: ${Colors.black};
    padding: 1rem;
  }
`

export const MenuGuideDownload = observer(() => {
  const { settings } = useRoot()
  const { loading, data } = useQMenuGuideDownload(
    { branchId: settings.branchId },
    { fetchPolicy: 'cache-first' },
  )
  if (loading || !(data && data.companyBranch.foodMenuUrl)) {
    return null
  }
  return (
    <MenuGuideDownloadButton color={Colors.greyMiddle}>
      <a
        href={data.companyBranch.foodMenuUrl}
        target="_blank"
        rel="noopener noreferrer"
      >
        <Trans>Menu codes manual</Trans>
      </a>
    </MenuGuideDownloadButton>
  )
})
