import { Trans } from '@lingui/macro'
import { ErrorOutline } from '@material-ui/icons'
import { useXFieldState } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { IconLine } from '../../../atoms/IconLine'
import { RM } from '../../../tools/ramda'
import { Spacing } from '../../../styling'
import { MenuAllergenIndicator } from '../atoms/MenuAllergenIndicator'
import { TRecipeFormValues } from '../menu.types'

const StyledAllergenLabel = styled.div`
  margin: 0.7rem 0 0 0.2rem;
`

export const RecipeAllergenDisplay = observer(() => {
  const field = useXFieldState<TRecipeFormValues['ingredients']>('ingredients')
  const allergens = RM.uniq(
    RM.flatten(RM.map(RM.prop('allergens'), field.value)),
  )
  return (
    <IconLine labelWidth={Spacing.DefaultLabelWidth} icon={ErrorOutline}>
      <StyledAllergenLabel>
        <Trans>Allergens based on stock cards</Trans>
      </StyledAllergenLabel>
      <MenuAllergenIndicator selected={allergens} />
    </IconLine>
  )
})
