import { useXFieldState, useXFormState } from '@speedlo/xform'
import { IObservableArray } from 'mobx'
import { observer } from 'mobx-react-lite'
import React from 'react'

import {
  FMenuAssocRecipeFragment,
  useQMenuRecipeFormAssoc,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { RM } from '../../../tools/ramda'
import { useSettingsBranch } from '../../../hooks/useSettingsBranch'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { useMenuItemId } from '../hooks/useMenuItemId'
import { TAssocRecipe, TPriceType } from '../menu.types'
import { RecipeAssocListTable } from '../molecules/RecipeAssocListTable'

interface IProps {
  priceType: TPriceType
}

export const RecipeAssociatedView = observer<IProps>(({ priceType }) => {
  const name = 'associatedRecipes'

  const recipes = useXFieldState<IObservableArray<TAssocRecipe>>(name)

  const recipeId = useMenuItemId()
  const branchId = useSettingsBranch()

  const { data, loading } = useQMenuRecipeFormAssoc({ branchId })

  const isPriceType = RM.propEq('warePriceType', priceType)
  const isAssocRecipe = RM.propEq('associatedRecipe')

  const priceTypeRecipes = RM.filter(isPriceType, recipes.value)

  const isAssociated = (id: ID) => priceTypeRecipes.some(isAssocRecipe(id))

  const onAddRecipe = React.useCallback(
    (addedRecipe: FMenuAssocRecipeFragment) => {
      recipes.value.push({
        warePriceType: priceType,
        associatedRecipe: addedRecipe.id,
        nameLabel: addedRecipe.nameLabel!,
        formattedPrice: '?',
      })
    },
    [priceType, recipes],
  )

  const onRemoveRecipe = React.useCallback(
    (id: ID) => {
      recipes.value.remove(
        recipes.value.find(RM.both(isAssocRecipe(id), isPriceType))!,
      )
    },
    [isAssocRecipe, isPriceType, recipes.value],
  )

  const { isSubmitting } = useXFormState()

  const rejectSuggestion = (recipe: FMenuAssocRecipeFragment) =>
    recipe.id === recipeId || isAssociated(recipe.id)

  return (
    <>
      <RecipeAssocListTable
        recipes={priceTypeRecipes}
        onRemove={onRemoveRecipe}
      />
      <MenuSuggestionPanel
        options={data ? RM.reject(rejectSuggestion, data.recipes) : ARRAY_EMPTY}
        onAdd={onAddRecipe}
        isLoading={loading}
        isDisabled={isSubmitting}
      />
    </>
  )
})
