import React from 'react'

import {
  Column,
  ColumnRendererWithNote,
  DataTable,
} from '../../../atoms/DataTable'
import { i18n } from '../../../i18n'
import { TouchButtonRemove } from '../../../molecules/TouchButtonRemove'
import { Spacing } from '../../../styling'
import { TAssocRecipe } from '../menu.types'

interface IProps {
  recipes: TAssocRecipe[]
  onRemove(recipeId: ID): void
}

export const RecipeAssocListTable: React.FC<IProps> = ({
  recipes,
  onRemove,
}) => (
  <DataTable items={recipes} disableHeader={true}>
    <Column
      dataKey="associatedRecipe"
      width={Spacing.ButtonColumnWidthPx}
      minWidth={Spacing.ButtonColumnWidthPx}
      cellRenderer={({ cellData }) => (
        <TouchButtonRemove onClick={() => onRemove(cellData)} />
      )}
    />
    <Column
      dataKey="nameLabel"
      width={300}
      label={i18n.t`Name`}
      cellRenderer={ColumnRendererWithNote}
      columnData={{ noteKey: 'internalNote' }}
    />
    <Column dataKey="formattedPrice" width={100} label={i18n.t`Price`} />
  </DataTable>
)
