import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { Column } from '../../../atoms/DataTable'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { Headline } from '../../../atoms/Headline'
import { useRoot } from '../../../atoms/RootProvider'
import {
  OrderOriginCategoryEnum,
  useMMenuCategorySort,
  useMMenuCategoryVisibility,
  useQMenuCategoryList,
  useQMenuCategoryOrigins,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { Colors } from '../../../styling'
import { MenuCreateButton } from '../atoms/MenuCreateButton'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { CategoryListTable } from '../molecules/CategoryListTable'

interface IProps {
  headline: string
  subHeadline?: string
  categoryType: WareCategoryTypeEnum
  heightStretch?: boolean
}

const StyledCategoryList = styled.div`
  .origins span {
    display: inline-block;
    margin: 0.2rem;
    padding: 0.2rem;
    background-color: ${Colors.greyLight};
  }
`

const CategoryList: React.FC<IProps> = ({
  headline,
  subHeadline = false,
  categoryType,
}) => {
  const { settings } = useRoot()
  const { getStash, loading, updateQuery, refetch } = useQMenuCategoryList({
    branchId: settings.branchId,
    categoryType,
  })

  const { data: dataOrigins } = useQMenuCategoryOrigins(
    {},
    { fetchPolicy: 'cache-first' },
  )

  const originsMap = React.useMemo(() => {
    if (dataOrigins && dataOrigins.origins) {
      return dataOrigins.origins.reduce((acc, origin) => {
        acc[origin.enum] = origin.code
        return acc
      }, {}) as Record<OrderOriginCategoryEnum, string>
    }
    return {} as Record<OrderOriginCategoryEnum, string>
  }, [dataOrigins])

  const renderOrigins = React.useCallback(
    ({ cellData }) => {
      cellData.sort()
      return cellData.map(originEnum => (
        <span key={originEnum}>{originsMap[originEnum]}</span>
      ))
    },
    [originsMap],
  )

  const [changeVisibility] = useMMenuCategoryVisibility()
  const onVisibilityToogle = React.useCallback(
    (categoryId: ID, visible: boolean) => {
      changeVisibility(
        { categoryId, visible },
        {
          optimisticResponse: {
            updateWareCategory: {
              __typename: 'WareCategory',
              id: categoryId,
              visible,
            },
          },
        },
      )
    },
    [changeVisibility],
  )

  const [sortCategory] = useMMenuCategorySort()
  const onSort = React.useCallback(
    async ({ item, items, offset }) => {
      updateQuery(() => ({
        categories: items,
      }))
      const result = await sortCategory({
        categoryId: item.id,
        offset,
      })
      if (result) {
        await refetch()
        return result.success
      }
      return false
    },
    [refetch, sortCategory, updateQuery],
  )

  const categories = getStash(data => data.categories)

  const { getCategoryCreateRoute } = useMenuRouting()

  return (
    <StyledCategoryList>
      <FlexRowSingle>
        <FlexColumn widthGrowIndex={1}>
          <Headline>{headline}</Headline>
          {subHeadline && <Headline size="smaller">{subHeadline}</Headline>}
        </FlexColumn>
        <FlexColumn fixedWidth={true}>
          <MenuCreateButton routeTo={getCategoryCreateRoute(categoryType)} />
        </FlexColumn>
      </FlexRowSingle>
      <CategoryListTable
        isLoading={loading}
        categories={categories || []}
        onToggle={onVisibilityToogle}
        isSortable={true}
        showHeader={false}
        showColor={true}
        fullHeight={true}
        showVisibilityColumn={true}
        onSort={onSort}
      >
        <Column
          dataKey="origins"
          width={300}
          className="origins"
          cellRenderer={renderOrigins}
          style={{ fontSize: '0.8rem' }}
        />
      </CategoryListTable>
    </StyledCategoryList>
  )
}

export default observer(CategoryList)
