import { observer } from 'mobx-react-lite'
import React from 'react'

import { useQMenuCategoryFormTrans } from '../../../graph/generated'
import { useMenuModel } from '../hooks/useMenu'
import { useMenuTranslations } from '../hooks/useMenuTranslations'

type TProps = NoChildren & {
  categoryId: ID
  isCreate: boolean
}

export const CategoryTranslationLoader = observer<TProps>(
  ({ categoryId, isCreate }) => {
    const menuModel = useMenuModel()
    const { shouldLoadTranslation, initTranslation } = useMenuTranslations()

    const language = menuModel.currentLanguage
    const { getStash } = useQMenuCategoryFormTrans(
      { language, categoryId },
      { fetchPolicy: 'no-cache', skip: isCreate || !shouldLoadTranslation() },
    )

    const translated = getStash(
      ({ category: { nameLabel, internalNoteLabel } }) => ({
        nameLabel,
        internalNoteLabel,
      }),
    )

    React.useEffect(() => {
      if (translated) {
        initTranslation(language, translated)
      }
    }, [initTranslation, language, translated])

    return null
  },
)
