import React from 'react'

import { FormGrid } from '../../../atoms/FormGrid'
import { FormDeleteButton } from '../../../components/forms/FormDeleteButton'
import { useMMenuStockDelete } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { FormLayoutGrid } from '../atoms/FormLayoutGrid'
import { MenuCodeInput } from '../molecules/MenuCodeInput'
import { MenuInternalNoteInput } from '../molecules/MenuInternalNoteField'
import { MenuNameInput } from '../molecules/MenuNameInput'
import { StockAllergenField } from '../molecules/StockAllergenField'
import { StockAllowNegativeField } from '../molecules/StockAllowNegativeField'
import { StockPluField } from '../molecules/StockPluField'
import { StockWareUnitField } from '../molecules/StockWareUnitField'

export const StockFormPrimary: React.FC = () => {
  const [deleteStockItem] = useMMenuStockDelete()
  return (
    <FormLayoutGrid
      primaryColumnWidth="minmax(10rem, 54rem)"
      contentPrimary={
        <FormGrid>
          <MenuNameInput placeholder={i18n.t`Card name`} />
          <MenuCodeInput placeholder={i18n.t`Card code`} />
          <StockWareUnitField />
          <StockPluField />
          <MenuInternalNoteInput
            fieldName="note"
            placeholder={i18n.t`Card note`}
            label={i18n.t`Card note`}
          />
          <StockAllergenField />
          <StockAllowNegativeField />
        </FormGrid>
      }
      contentBottomRight={
        <FormDeleteButton
          mutationName="destroyStockItem"
          confirmText={i18n.t`Remove this card?`}
          onDelete={stockId => deleteStockItem({ stockId })}
        />
      }
    />
  )
}
