import { XFieldName } from '@speedlo/xform'
import React from 'react'
import styled from 'styled-components'

import { QZoneColorsZoneColors } from '../graph/generated'
import { Colors } from '../styling'
import { idProp, RM } from '../tools/ramda'
import { FormDropdownSingle } from './forms/FormDropdownSingle'

interface IProps {
  fieldName: XFieldName
  colors: RoA<TColor>
  isLoading: boolean
}

export type TColor = {
  id: ID
  color: string
  code: string
}

const ColorOption = styled.div`
  background-color: ${({ color }: { color: string }) => color};
  color: ${Colors.white};
  width: 6rem;
  padding: 0.4rem 1rem;
  margin: 0;
  height: 100%;
`

const makeStyles = () => ({
  option: base => ({
    ...base,
    padding: 0,
    margin: 0,
  }),
  menu: base => ({
    ...base,
    width: '8rem',
    maxWidth: '8rem',
  }),
})

export const ColorSelector: React.FC<IProps> = ({
  fieldName,
  colors,
  isLoading,
}) => {
  return (
    <FormDropdownSingle<QZoneColorsZoneColors>
      fieldName={fieldName}
      options={colors}
      isLoading={isLoading}
      width={8}
      styles={makeStyles()}
      defaultValue={RM.head(colors)}
      getOptionValue={idProp}
      formatOptionLabel={({ color, code }) => (
        <ColorOption color={color}>{code}</ColorOption>
      )}
    />
  )
}
