import { useRouterContext } from '@speedlo/hooks'
import { TValidationSchema, useXForm, XFormErrors } from '@speedlo/xform'
import React from 'react'

import { logForm } from '../../helpers/logger'
import { useSnackBar } from '../../hooks/useSnackBar'

interface IOptions<TValues> {
  initialValues: TValues
  loadValues?(initialValues: TValues): Promise<Partial<TValues> | undefined>
  onSubmit(values: TValues): Promise<any>
  submitMessage?: string
  submitRedirectPath?: string
  validationSchema?: TValidationSchema
}

export type TFormLoaderOptions<TValues = any> = IOptions<TValues>

const logger =
  process.env.REACT_APP_STAGE !== 'prod' ? obj => logForm('%o', obj) : undefined

export function useFormLoader<TValues>({
  onSubmit,
  initialValues,
  loadValues,
  validationSchema,
  submitMessage,
  submitRedirectPath,
}: IOptions<TValues>) {
  const { history } = useRouterContext()
  const { enqueueSuccess, enqueueWarning } = useSnackBar()

  const onSubmitSuccess = React.useCallback(
    async (values: TValues) => {
      await onSubmit(values)
      if (submitMessage) {
        enqueueSuccess(submitMessage)
      }
      if (submitRedirectPath) {
        history.push(submitRedirectPath)
      }
    },
    [enqueueSuccess, history, onSubmit, submitMessage, submitRedirectPath],
  )

  const onSubmitFailed = React.useCallback(
    (values: TValues, errors: XFormErrors) => {
      for (const msg of errors.values()) {
        if (msg) {
          enqueueWarning(msg)
        }
      }
    },
    [enqueueWarning],
  )

  const hasLoaded = React.useRef(false)

  const onReset = React.useCallback(() => {
    hasLoaded.current = false
  }, [])

  const xform = useXForm<TValues>({
    onSubmit: onSubmitSuccess,
    onSubmitFailed,
    onReset,
    validationSchema,
    initialValues,
    initialValidationEnabled: loadValues === undefined,
    ignoreUnknownFieldsInValidation: true,
    onFormChanges: logger,
  })

  React.useEffect(() => {
    if (!hasLoaded.current) {
      if (loadValues) {
        Promise.resolve(loadValues(xform.state.initialValues)).then(values => {
          if (values) {
            xform.api.mergeValues(values)
            xform.state.pristineValues = xform.state.values
          }
          xform.api.enableValidation()
        })
      }
      hasLoaded.current = true
    }
  }, [loadValues, xform])

  return xform
}
