import { Trans } from '@lingui/macro'
import { useXField, useXFormState } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { useDropzone } from 'react-dropzone'
import styled from 'styled-components/macro'

import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Colors, Spacing } from '../../../styling'
import { TRecipeFormValues } from '../menu.types'
import { ImageCropModal } from './ImageCropModal'

const StyledRecipeImageUploader = styled.div`
  border: 1px dotted ${Colors.greyMiddle};
  background: ${Colors.white};
  color: ${Colors.greyMiddle};
  width: 16rem;
  height: 16rem;
  cursor: pointer;

  & section {
    height: 100%;
    width: 100%;
  }

  & div.container {
    height: 100%;
    width: 100%;
    padding: 0.4rem;
  }

  & img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }

  & div.hint {
    margin: 6rem 0;
    text-align: center;
  }

  & .delete-button {
    position: relative;
    top: ${`-${Spacing.ButtonHeight}${Spacing.DefaultSizeUnit}`};
    left: 12.5rem;
  }
`

export const RecipeImage = observer<NoChildren>(() => {
  const { enqueueWarning } = useSnackBar()
  const { isSubmitting } = useXFormState()

  const [uploadedImage, setUploadedImage] = React.useState<Maybe<File>>()

  const { isDragActive, getRootProps, getInputProps } = useDropzone({
    disabled: isSubmitting,
    accept: 'image/*',
    multiple: false,
    onDropAccepted: ([firstImage]) => {
      setUploadedImage(firstImage)
    },
    onDropRejected: () => {
      enqueueWarning(i18n.t`Supplied file is not recognized as image.`)
    },
  })

  const field = useXField<TRecipeFormValues['image']>('image')

  const imageDisplay = React.useMemo(() => {
    const image = field.value
    if (image === null) {
      return null
    }
    if (typeof field.value !== 'string') {
      return URL.createObjectURL(image)
    } else {
      return field.value
    }
  }, [field.value])

  React.useEffect(() => {
    return () => {
      if (imageDisplay) {
        URL.revokeObjectURL(imageDisplay)
      }
    }
  }, [imageDisplay])

  return (
    <StyledRecipeImageUploader>
      <section>
        <div className="container" {...getRootProps()}>
          <input {...getInputProps()} />
          {imageDisplay && <img src={imageDisplay} alt="" />}
          {imageDisplay === null && (
            <div className="hint">
              {isDragActive ? (
                <Trans>Drop the image here ...</Trans>
              ) : (
                <Trans>Click to select image or drop it here</Trans>
              )}
            </div>
          )}
        </div>
      </section>
      {imageDisplay && (
        <TouchButtonDelete
          type="button"
          disabled={isSubmitting}
          onDelete={async () => {
            field.setValue(null)
          }}
        />
      )}
      {uploadedImage && (
        <ImageCropModal
          image={uploadedImage}
          onConfirm={blob => {
            setUploadedImage(null)
            field.setValue(blob)
          }}
          onCancel={() => {
            setUploadedImage(null)
          }}
        />
      )}
    </StyledRecipeImageUploader>
  )
})
