import { useXField, useXFormState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import {
  ISelectControlFlatProps,
  SelectControlFlat,
} from '../../atoms/SelectControlFlat'
import { idProp } from '../../tools/ramda'

interface IProps<ItemType>
  extends Omit<
    ISelectControlFlatProps<ItemType>,
    | 'selectedItems'
    | 'onMultiSelection'
    | 'selectedItem'
    | 'onSingleSelection'
    | 'isMulti'
  > {
  fieldName: XFieldName
  getItemId?: (item: ItemType) => ID
}

export const FormFlatSingleSelect = <ItemType extends Record<string, unknown>>(
  props: IProps<ItemType>,
) => {
  const {
    items,
    fieldName,
    isClearable = false,
    getItemId = idProp as AnyFunction,
  } = props

  const field = useXField(fieldName)
  const formState = useXFormState()

  return useObserver(() => (
    <SelectControlFlat<ItemType>
      selectedItem={
        field.value && items.find(item => getItemId(item) === field.value)
      }
      onSingleSelection={item => {
        field.setValue(item ? getItemId(item) : null)
        field.setTouched()
      }}
      disabled={formState.isSubmitting}
      isInvalid={field.hasError}
      isClearable={isClearable}
      isMulti={false}
      {...props}
    />
  ))
}
