import { Label } from '@material-ui/icons'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormInput, FormInputProps } from '../../../components/forms/FormInput'
import { Spacing } from '../../../styling'
import { MenuTranslationInput } from '../atoms/MenuTranslationInput'

interface IProps extends Partial<FormInputProps> {
  localized?: boolean
}

export const MenuNameInput: React.FC<IProps> = ({
  fieldName = 'nameLabel',
  localized = false,
  ...rest
}) => {
  const props = {
    fieldName,
    ...rest,
  }
  return (
    <IconLine icon={Label} labelWidth={Spacing.DefaultLabelWidth}>
      {localized ? (
        <MenuTranslationInput {...props} />
      ) : (
        <FormInput {...props} />
      )}
    </IconLine>
  )
}
