import { useRouterContext } from '@speedlo/hooks'
import { useXField, useXFormState, XFieldName } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useAccessGuard } from '../../hooks/useAccessGuard'
import { TouchButtonDelete } from '../../molecules/TouchButtonDelete'

type TProps = NoChildren & {
  fieldName?: XFieldName
  mutationName?: string
  confirmText: string
  onDelete(id: ID): Promise<any>
}

export const FormDeleteButton = observer<TProps>(
  ({ fieldName = 'id', mutationName, confirmText, onDelete }) => {
    const field = useXField<ID>(fieldName)
    const canDelete = useAccessGuard([], mutationName ? [mutationName] : [])

    const { history } = useRouterContext()
    const { isSubmitting } = useXFormState()

    if (!canDelete || field.value === 'NEW') {
      return null
    }

    return (
      <TouchButtonDelete
        type="button"
        disabled={isSubmitting}
        confirmText={confirmText}
        onDelete={async () => {
          await onDelete(field.value)
          history.goBack()
        }}
      />
    )
  },
)
