import React from 'react'

import { FormGrid } from '../../../atoms/FormGrid'
import { IconLine } from '../../../atoms/IconLine'
import { FormDeleteButton } from '../../../components/forms/FormDeleteButton'
import { FormCheckbox } from '../../../components/forms/FormInputCheckbox'
import { useMMenuCategoryDelete } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { FormLayoutGrid } from '../atoms/FormLayoutGrid'
import { IsMenuItemType } from '../atoms/IsMenuItemType'
import { CategoryColorField } from '../molecules/CategoryColorField'
import { CategoryOriginsField } from '../molecules/CategoryOriginsField'
import { CategoryPictogramSelect } from '../molecules/CategoryPictogramSelect'
import { MenuInternalNoteInput } from '../molecules/MenuInternalNoteField'
import { MenuLanguageSwitch } from '../molecules/MenuLanguageSwitch'
import { MenuNameInput } from '../molecules/MenuNameInput'

interface IProps {}

export const CategoryFormPrimary: React.FC<IProps> = () => {
  const [deleteCategory] = useMMenuCategoryDelete()
  return (
    <FormLayoutGrid
      primaryColumnWidth="minmax(10rem, 45rem)"
      secondaryColumnWidth="14rem"
      contentPrimary={
        <FormGrid>
          <MenuNameInput placeholder={i18n.t`Category name`} localized />
          <MenuInternalNoteInput
            placeholder={i18n.t`Category internal note`}
            fieldName="internalNoteLabel"
            localized
          />
          <CategoryOriginsField />
          <IconLine>
            <FormCheckbox
              fieldName="visible"
              label={i18n.t`To show this category`}
            />
          </IconLine>
        </FormGrid>
      }
      contentSecondary={
        <IsMenuItemType isMain>
          <CategoryPictogramSelect />
          <CategoryColorField />
        </IsMenuItemType>
      }
      contentTopRight={<MenuLanguageSwitch />}
      contentBottomRight={
        <FormDeleteButton
          mutationName="destroyWareCategory"
          confirmText={i18n.t`Remove this category?`}
          onDelete={categoryId => deleteCategory({ categoryId })}
        />
      }
    />
  )
}
