import { observer } from 'mobx-react-lite'
import React from 'react'

import { useQMenuStockList } from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { useMenuModel } from '../hooks/useMenu'
import { StockListTable } from './StockListTable'

interface IProps {}

export const StockListLoader = observer<IProps>(() => {
  const menuModel = useMenuModel()
  const { data, loading } = useQMenuStockList({
    branchId: menuModel.branchId,
  })

  const filterItem = menuModel.filter.getPredicate({
    partialMatch: ['code', 'nameLabel'],
    exactMatch: ['plu'],
  })

  const stockItems = data ? data.stockItems.filter(filterItem) : ARRAY_EMPTY

  return <StockListTable stockItems={stockItems} isLoading={loading} />
})
