import { Trans } from '@lingui/macro'
import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { Card } from '../../../atoms/Card'
import { Headline } from '../../../atoms/Headline'
import { MarginBox } from '../../../atoms/MarginBox'
import { NoteText } from '../../../atoms/NoteText'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { i18n } from '../../../i18n'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { TopRightButtonContainer } from '../../../molecules/TopRightButtonContainer'
import { IsMenuItemType } from '../atoms/IsMenuItemType'
import { MenuSubmitButton } from '../atoms/MenuSubmitButton'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { getRecipeTypeLabel } from '../menu.labels'
import { TCategoryType } from '../menu.types'
import { RecipeCategoryView } from '../organisms/RecipeCategoryView'
import { RecipeFormLoader } from '../organisms/RecipeFormLoader'
import { RecipeFormPricing } from '../organisms/RecipeFormPricing'
import { RecipeFormPrimary } from '../organisms/RecipeFormPrimary'
import { RecipeIngredientsView } from '../organisms/RecipeIngredientsView'

interface IProps extends RouteComponentProps<{ recipeId: ID }> {}

export const MenuRecipePage: React.FC<IProps> = ({ match }) => {
  const { recipeId } = match.params
  const { isCreate, itemType, redirectToRecipeList } = useMenuRouting(recipeId)

  if (itemType === null) {
    redirectToRecipeList()
    return null
  }

  return (
    <RecipeFormLoader
      isCreate={isCreate}
      recipeId={recipeId}
      recipeType={itemType}
    >
      <MarginBox right={1.25} bottom={4}>
        <FullPageLayout
          pageTitle={getRecipeTypeLabel(itemType)}
          hideLogoutWidget={true}
          useBackButton={true}
        >
          <Card>
            <RecipeFormPrimary isCreate={isCreate} />
          </Card>
          <Card>
            <RecipeFormPricing />
          </Card>
          <Card>
            <Headline>
              <Trans>Recipe ingredients</Trans>
            </Headline>
            <VerticalGap />
            <RecipeIngredientsView />
          </Card>
          <IsMenuItemType isMain>
            <Card>
              <Headline>
                <Trans>Sidedish categories</Trans>
              </Headline>
              <NoteText>
                <Trans>These sidedish are presented with recipe</Trans>
              </NoteText>
              <RecipeCategoryView
                fieldName="sideDishes"
                addButtonText={i18n.t`Attach sidedish category`}
                noRowsText={i18n.t`Recipe has no sidedish category attached`}
              />
            </Card>
            <Card>
              <Headline>
                <Trans>Category assignments</Trans>
              </Headline>
              <NoteText>
                <Trans>Recipe is in these categories</Trans>
              </NoteText>
              <RecipeCategoryView
                fieldName="categories"
                categoryType={TCategoryType.Main}
                addButtonText={i18n.t`Add to category`}
                noRowsText={i18n.t`Recipe is not in any category`}
              />
            </Card>
          </IsMenuItemType>
        </FullPageLayout>

        <TopRightButtonContainer>
          <MenuSubmitButton
            isCreate={isCreate}
            createMutation="createRecipe"
            updateMutation="updateRecipe"
          />
        </TopRightButtonContainer>
      </MarginBox>
    </RecipeFormLoader>
  )
}
