import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormDropdownMulti } from '../../../components/forms/FormDropdownMulti'
import {
  QMenuCategoryOriginsOrigins,
  useQMenuCategoryOrigins,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { i18n } from '../../../i18n'
import { RM } from '../../../tools/ramda'

export const CategoryOriginsField: React.FC = () => {
  const { data, loading } = useQMenuCategoryOrigins()
  return (
    <IconLine label={i18n.t`cat. origins`} labelWidth={7}>
      <FormDropdownMulti<QMenuCategoryOriginsOrigins>
        fieldName="origins"
        isLoading={loading}
        options={data ? data.origins : ARRAY_EMPTY}
        getOptionLabel={RM.prop('code')}
        getOptionValue={RM.prop('enum')}
      />
    </IconLine>
  )
}
