import { ErrorOutline } from '@material-ui/icons'
import { cn } from '@speedlo/ui'
import { useXFormState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { ITouchButtonProps, TouchButton } from '../../molecules/TouchButton'
import { Colors } from '../../styling'

interface IProps extends ITouchButtonProps {
  children: ReactNode | ReactNode[] | string
}

export const FormSubmitButton: React.FC<IProps> = ({
  children,
  id = 'form-submit',
  ...props
}) => {
  const state = useXFormState()
  return useObserver(() => (
    <TouchButton
      id={id}
      type="submit"
      color={Colors.green}
      disabled={state.isSubmitting}
      icon={!state.isValid ? ErrorOutline : undefined}
      iconSize={1.1}
      iconColor={Colors.red}
      {...props}
      className={cn(props.className, 'animated', 'infinite', {
        heartBeat: state.isSubmitting,
      })}
    >
      <>{children}</>
    </TouchButton>
  ))
}
