import { useXField, useXFormState } from '@speedlo/xform'
import { observer, useObserver } from 'mobx-react-lite'
import React from 'react'

import {
  FMenuStockItemFragment,
  useQMenuStockList,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { RM } from '../../../tools/ramda'
import { useSettingsBranch } from '../../../hooks/useSettingsBranch'
import { i18n } from '../../../i18n'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { TIngredient, TRecipeFormValues } from '../menu.types'
import { RecipeIngredientsTable } from '../molecules/RecipeIngredientsTable'

interface IProps {
  ingredients: TIngredient[]
  onAdd(ingredient: TIngredient): void
  onRemove(stockItemId: ID): void
}

const unselectedStockOnly = RM.differenceWith(
  ({ id }: FMenuStockItemFragment, { stockItem }: TIngredient) =>
    id === stockItem,
)

export const RecipeIngredientsControl = observer<IProps>(
  ({ ingredients, onAdd, onRemove }) => {
    const branchId = useSettingsBranch()
    const { data, loading } = useQMenuStockList({
      branchId,
    })

    const { isSubmitting } = useXFormState()

    return (
      <>
        <RecipeIngredientsTable ingredients={ingredients} onRemove={onRemove} />
        <MenuSuggestionPanel<FMenuStockItemFragment>
          isLoading={loading}
          isDisabled={isSubmitting}
          addButtonText={i18n.t`Add ingredient`}
          options={
            data
              ? unselectedStockOnly(data.stockItems, ingredients)
              : ARRAY_EMPTY
          }
          onAdd={(addedStockItem: FMenuStockItemFragment) => {
            onAdd({
              stockItem: addedStockItem.id,
              label: addedStockItem.nameLabel,
              quantity: '1',
              unitLabel: addedStockItem.wareUnit.code,
              allergens: addedStockItem.allergens.map(RM.prop('enum')),
            })
          }}
        />
      </>
    )
  },
)

export const RecipeIngredientsView = () => {
  const field = useXField<TRecipeFormValues['ingredients']>('ingredients')
  return useObserver(() => (
    <RecipeIngredientsControl
      ingredients={field.value}
      onAdd={ingredient => {
        field.value.push(ingredient)
      }}
      onRemove={stockItemId => {
        field.value.splice(
          field.value.findIndex(RM.propEq('stockItem', stockItemId)),
          1,
        )
      }}
    />
  ))
}
