import { useXField, useXFormState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import {
  arrayMove,
  SortableContainer,
  SortableElement,
  SortEndHandler,
} from 'react-sortable-hoc'
import styled from 'styled-components/macro'

import { mediaDesktop, useResponsive } from '../../../app/Responsive'
import { DataList } from '../../../atoms/DataList'
import { DragHandle } from '../../../atoms/DragHandle'
import { NoteText } from '../../../atoms/NoteText'
import {
  FMenuCategoryInFormRecipes,
  FMenuRecipeInListFragment,
  useQMenuRecipeList,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { formatPrice } from '../../../helpers/formats'
import { RM } from '../../../tools/ramda'
import { useSettingsBranch } from '../../../hooks/useSettingsBranch'
import { i18n } from '../../../i18n'
import { TouchButtonRemove } from '../../../molecules/TouchButtonRemove'
import { Spacing } from '../../../styling'
import { idPropEq } from '../../../tools/ramda'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { useMenuItemType } from '../hooks/useMenuItemType'
import { usePriceType } from '../hooks/usePriceType'

interface IProps {}

const StyledCategoryRecipesView = styled.div`
  display: flex;
  flex-direction: column;
  height: calc(100% - 2rem);
  width: 100%;

  & .fixed-row {
    flex: 0 0 auto;
  }
  & .move-cell {
    ${mediaDesktop`
      cursor: move;
    `}
  }
`

const StyledFormStretchedRow = styled.div`
  flex: 1 1 0;
  margin: 1rem 0 0 1rem;
`

const SortableRecipes = SortableContainer(StyledFormStretchedRow)

const StyledRecipeRow = styled.div`
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: ${Spacing.ButtonColumnWidthPx}px 100px 3fr 1fr 1fr 50px;
`

type TCategoryRecipe = FMenuCategoryInFormRecipes

interface IRecipeRow {
  recipe: TCategoryRecipe
  onRemove(): void
}

const SortableRecipe = SortableElement(({ recipe, onRemove }: IRecipeRow) => {
  const { priceTypes, priceTypeLabels } = usePriceType()
  const prices = priceTypes.map(priceType => {
    const foundPrice = recipe.prices.find(
      RM.pathEq(['priceType', 'enum'], priceType),
    )
    return (
      <div className="move-cell" key={priceType}>
        <NoteText>{priceTypeLabels[priceType]}</NoteText>
        {foundPrice ? (
          formatPrice(foundPrice.price.value, foundPrice.price.currency.code)
        ) : (
          <i>{i18n.t`Not set`.toLocaleLowerCase()}</i>
        )}
      </div>
    )
  })

  return (
    <StyledRecipeRow>
      <div>
        <TouchButtonRemove onClick={onRemove} />
      </div>
      <div className="move-cell">{recipe.code}</div>
      <div className="move-cell">
        {recipe.nameLabel}
        <NoteText>{recipe.internalNote || ''}</NoteText>
      </div>
      {prices}
      <DragHandle />
    </StyledRecipeRow>
  )
})

export const CategoryRecipesView: React.FC<IProps> = () => {
  const branchId = useSettingsBranch()
  const categoryType = useMenuItemType()

  const { data, loading } = useQMenuRecipeList({
    branchId,
    categoryType,
  })

  const field = useXField<TCategoryRecipe[]>('recipes')

  const onHandleSorting: SortEndHandler = React.useCallback(
    async ({ newIndex, oldIndex }) => {
      if (newIndex === oldIndex) {
        return
      }
      field.setValue(arrayMove(field.value, oldIndex, newIndex))
    },
    [field],
  )

  const unselectedRecipesOnly = RM.differenceWith<
    FMenuRecipeInListFragment,
    TCategoryRecipe
  >(RM.eqProps('id'))

  const { isDesktop } = useResponsive()
  const formState = useXFormState()

  return useObserver(() => (
    <StyledCategoryRecipesView>
      <div className="fixed-row">
        <MenuSuggestionPanel<TCategoryRecipe>
          isLoading={loading}
          isDisabled={formState.isSubmitting}
          options={
            data
              ? unselectedRecipesOnly(data.recipes, field.value)
              : ARRAY_EMPTY
          }
          onAdd={addedRecipe => field.value.push(addedRecipe)}
          getOptionLabel={({ nameLabel, code }) => `${code} - ${nameLabel}`}
        />
      </div>
      <SortableRecipes
        lockAxis="y"
        lockToContainerEdges={true}
        onSortEnd={onHandleSorting}
        useDragHandle={!isDesktop}
      >
        <DataList
          items={field.value}
          heightStretch={true}
          rowHeight={60}
          renderItem={(recipe, index) => (
            <SortableRecipe
              recipe={recipe}
              onRemove={() => {
                field.setValue(RM.reject(idPropEq(recipe.id), field.value))
              }}
              index={index}
            />
          )}
        />
      </SortableRecipes>
    </StyledCategoryRecipesView>
  ))
}
