import React from 'react'
import styled from 'styled-components/macro'

import { mediaMobile } from '../../../app/Responsive'
import { Spacing } from '../../../styling'

interface IStyledProps {
  primaryColumnWidth?: string
  secondaryColumnWidth?: string
}

interface IProps extends IStyledProps {
  contentPrimary: React.ReactNode
  contentSecondary?: React.ReactNode
  contentTopRight?: React.ReactNode
  contentBottomRight?: React.ReactNode
}

const StyledFormLayoutGrid = styled.div<IStyledProps>`
  display: grid;
  margin: -${Spacing.Middle};
  grid-column-gap: ${Spacing.Middle};
  grid-template-columns: ${Spacing.Middle} ${({ primaryColumnWidth }) =>
      primaryColumnWidth} ${({ secondaryColumnWidth }) => secondaryColumnWidth} auto;
  grid-template-rows: ${Spacing.Middle} auto 1fr auto ${Spacing.Middle};
  grid-template-areas:
    '. .              .                contentTopRight'
    '. contentPrimary contentSecondary contentTopRight'
    '. contentPrimary contentSecondary .'
    '. contentPrimary contentSecondary contentBottomRight'
    '. .              .                contentBottomRight';

  .contentPrimary {
    grid-area: contentPrimary;
  }
  .contentSecondary {
    grid-area: contentSecondary;
  }
  .contentTopRight {
    grid-area: contentTopRight;
    justify-self: end;
    align-self: start;
  }
  .contentBottomRight {
    grid-area: contentBottomRight;
    justify-self: end;
    align-self: end;
  }

  ${mediaMobile`
    grid-template-columns: ${Spacing.Middle} 1fr ${Spacing.Middle};
    grid-template-rows: auto auto auto auto;
    grid-row-gap: ${Spacing.Middle};
    grid-template-areas:
      '. contentTopRight    contentTopRight'
      '. contentPrimary     .'
      '. contentSecondary   .'
      '. contentBottomRight contentBottomRight'
  `};
`

export const FormLayoutGrid: React.FC<IProps> = ({
  contentPrimary,
  contentSecondary,
  contentTopRight,
  contentBottomRight,
  primaryColumnWidth = '1fr',
  secondaryColumnWidth = '0',
}) => {
  return (
    <StyledFormLayoutGrid
      primaryColumnWidth={primaryColumnWidth}
      secondaryColumnWidth={secondaryColumnWidth}
    >
      <div className="contentPrimary">{contentPrimary}</div>
      <div className="contentSecondary">{contentSecondary}</div>
      <div className="contentTopRight">{contentTopRight}</div>
      <div className="contentBottomRight">{contentBottomRight}</div>
    </StyledFormLayoutGrid>
  )
}
