import { getFlag } from '@speedlo/assets'
import { useXFieldForInput, useXFormState, XFieldName } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { StyledFormInput } from '../../../atoms/FormInputStyled'
import { i18n } from '../../../i18n'
import { useMenuModel } from '../hooks/useMenu'
import { useMenuTranslations } from '../hooks/useMenuTranslations'

interface IProps {
  fieldName: XFieldName
  placeholder?: string
}

const StyledMenuTranslationInput = styled.div`
  position: relative;

  .original {
    font-size: 0.9rem;
    padding: 0.3rem 0;
    opacity: 0.7;
  }

  .original img {
    width: 1.5rem;
    margin: 0 0.6rem;
  }

  .current-language {
    width: 1.5rem;
    position: absolute;
    right: 0.5rem;
    top: 0.8rem;
    border: 1px #aaa solid;
    box-sizing: content-box;
  }
`

export const MenuTranslationInput = observer<IProps>(
  ({ fieldName, placeholder }) => {
    const { isSubmitting } = useXFormState()
    const [field, getFieldProps] = useXFieldForInput(fieldName)
    const menuModel = useMenuModel()

    const { getTranslation, findTranslation } = useMenuTranslations()

    const onChange = (ev: React.ChangeEvent<HTMLInputElement>) => {
      const value = ev.target.value
      const translation = getTranslation(menuModel.currentLanguage)
      translation[field.name] = value
    }

    const isTranslating = !menuModel.isDefaultLanguageSelected

    const translation = findTranslation(menuModel.currentLanguage)
    const value = isTranslating
      ? translation && translation[field.name]
      : field.value

    const fieldProps = getFieldProps()

    return (
      <StyledMenuTranslationInput>
        <StyledFormInput
          {...fieldProps}
          disabled={isSubmitting}
          value={value || ''}
          onChange={isTranslating ? onChange : fieldProps.onChange}
          inputFullWidth={true}
          invalid={!isTranslating && field.touched && field.hasError}
          placeholder={placeholder}
        />
        <img
          className="current-language"
          src={getFlag(menuModel.currentLanguage)}
          alt="translation"
        />
        {isTranslating && (
          <div
            className="original"
            onClick={() => menuModel.setLanguage(menuModel.defaultLanguage)}
          >
            <img src={getFlag(menuModel.defaultLanguage)} alt="original text" />
            {field.value || <i>{i18n.t`No text is filled`}</i>}
          </div>
        )}
      </StyledMenuTranslationInput>
    )
  },
)
