import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { Sizer } from '../../../atoms/Sizer'
import { StyledCheckbox } from '../../../atoms/StyledCheckbox'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  id: string
  count: number
  label: ReactNode | string
  enabled: boolean
  onChange(count: number): void
}

const StyledCategoryRequirementControl = styled.div`
  display: flex;
  align-items: center;
  & > label {
    min-width: 5rem;
  }
  & > * {
    margin: 0.2rem 0.5rem;
  }
  input.count {
    font-size: 1.5rem;
    line-height: 1.5rem;
    text-align: center;
  }
`

export const CategoryRequirementControl: React.FC<IProps> = ({
  count,
  label,
  enabled,
  onChange,
  id,
}) => {
  const countRef = React.useRef(count)
  return (
    <StyledCategoryRequirementControl>
      <label htmlFor={id}>{label}</label>
      <Sizer width={3}>
        <FormInputStyled
          id={id}
          value={count === 0 ? '' : count}
          onChange={ev => {
            if (ev.target.validity.valid) {
              onChange(Number.parseInt(ev.target.value, 10))
            }
          }}
          onFocus={ev => {
            ev.target.select()
          }}
          disabled={count === 0 || !enabled}
          inputFullWidth={true}
          type="tel"
          pattern="^[1-9]\d*$"
          className="count"
        />
      </Sizer>
      <Sizer width={4}>
        <TouchButton
          disabled={!enabled || count <= 0}
          onClick={() => onChange(count - 1)}
        >
          ➖
        </TouchButton>
      </Sizer>
      <Sizer width={4}>
        <TouchButton disabled={!enabled} onClick={() => onChange(count + 1)}>
          ➕
        </TouchButton>
      </Sizer>
      <div>
        <StyledCheckbox>
          <input
            type="checkbox"
            id={`${id}-nolimit`}
            checked={count === 0}
            disabled={!enabled}
            onChange={ev => {
              if (count === 0) {
                onChange(countRef.current || 1)
              } else {
                countRef.current = count
                onChange(0)
              }
            }}
          />
          <label htmlFor={`${id}-nolimit`}>
            <Trans>Unlimited</Trans>
          </label>
        </StyledCheckbox>
      </div>
    </StyledCategoryRequirementControl>
  )
}
