import { useStateRef } from '@speedlo/hooks'
import React, { useCallback } from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import {
  SelectControl,
  TSelectControlProps,
} from '../../../atoms/SelectControl'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { RM } from '../../../tools/ramda'

interface IProps<OptionType> extends TSelectControlProps<OptionType> {
  onAdd(option: OptionType): void
  addLabel?: string
  addButtonText?: string
  isLoading?: boolean
}

export function MenuSuggestionPanel<
  OptionType extends { id: ID; nameLabel: Maybe<string> }
>(props: IProps<OptionType>) {
  const {
    onAdd,
    addLabel = i18n.t`Add`,
    addButtonText = i18n.t`Add`,
    isLoading = false,
    ...selectProps
  } = props

  const [getSelected, setSelected] = useStateRef<Maybe<OptionType>>(null)

  const onConfirm = useCallback(() => {
    const selected = getSelected()
    if (selected !== null) {
      onAdd(selected)
      setSelected(null)
    }
  }, [getSelected, onAdd, setSelected])

  const onKeyDown = useCallback(
    (ev: React.KeyboardEvent<HTMLElement>) => {
      if (ev.key === 'Enter') {
        onConfirm()
      }
    },
    [onConfirm],
  )

  const onSelect = useCallback(
    option => {
      setSelected(option || null)
    },
    [setSelected],
  )

  return (
    <FlexRowSingle justify="flex-start">
      <FlexColumn fixedWidth={true} width="4rem" margin="auto 0">
        {addLabel}
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="20rem" margin="auto 0">
        <SelectControl<OptionType>
          value={getSelected()}
          onChange={onSelect}
          onKeyDown={onKeyDown}
          isLoading={isLoading}
          isMulti={false}
          isClearable={false}
          getOptionValue={RM.prop('id')}
          getOptionLabel={RM.propOr('', 'nameLabel')}
          {...selectProps}
        />
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="10rem" margin="auto 0">
        <TouchButton
          text={addButtonText}
          disabled={getSelected() === null}
          onClick={onConfirm}
        />
      </FlexColumn>
    </FlexRowSingle>
  )
}
