import { TValidationSchema, XFormRender } from '@speedlo/xform'
import React from 'react'

import { useFormLoader } from '../../../components/forms/useFormLoader'

interface IProps<TValues> {
  isCreate: boolean
  initialValues: TValues
  loadUpdateValues(values: TValues): Promise<Partial<TValues> | undefined>
  loadCreateDefaults?(values: TValues): Promise<Partial<TValues> | undefined>
  onCreate(values: TValues): Promise<any>
  onUpdate(values: TValues): Promise<any>
  createSuccessMessage?: string
  updateSuccessMessage?: string
  successRedirectPath?: string
  validationSchema: TValidationSchema
  children: ReactNode | ReactNode[]
}

export function MenuFormLoader<TValues>({
  isCreate,
  initialValues,
  loadCreateDefaults,
  loadUpdateValues,
  onCreate,
  onUpdate,
  createSuccessMessage,
  updateSuccessMessage,
  successRedirectPath,
  validationSchema,
  children,
}: IProps<TValues>) {
  const xform = useFormLoader({
    initialValues,
    validationSchema,
    onSubmit: isCreate ? onCreate : onUpdate,
    loadValues: isCreate ? loadCreateDefaults : loadUpdateValues,
    submitMessage: isCreate ? createSuccessMessage : updateSuccessMessage,
    submitRedirectPath: successRedirectPath,
  })
  return <XFormRender xform={xform}>{children}</XFormRender>
}
