import { i18nMark } from '@lingui/core'
import { useXFormState } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormFlatSingleSelect } from '../../../components/forms/FormFlatSingleSelect'
import { FieldStateEnum } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { RM } from '../../../tools/ramda'
import { TStockFormValues } from '../menu.types'

const items = [
  {
    type: FieldStateEnum.Enabled,
    label: i18nMark('Permit'),
  },
  {
    type: FieldStateEnum.Disabled,
    label: i18nMark('Forbid'),
  },
]

export const StockAllowNegativeField = observer(() => {
  const { allowNegativeDefault } = useXFormState<TStockFormValues>().values
  return (
    <IconLine label={i18n.t`Negative stock`} labelWidth={7}>
      <FormFlatSingleSelect
        items={items}
        fieldName="allowNegative"
        getItemId={RM.prop('type')}
        formatItem={item => {
          if (item.type === allowNegativeDefault) {
            return (
              <span>
                {i18n._(item.label)}
                <i>{` (${i18n.t`Is default setting`.toLocaleLowerCase()})`}</i>
              </span>
            )
          }
          return i18n._(item.label)
        }}
      />
    </IconLine>
  )
})
