import { cn } from '@speedlo/ui'
import React from 'react'
import styled from 'styled-components'

import { AllergenEnum, useQMenuAllergens } from '../../../graph/generated'
import { Colors, Spacing } from '../../../styling'

interface IProps {
  selected: RoA<AllergenEnum>
  onToggle?(allergen: AllergenEnum): void
}

const AllergenBox = styled.div`
  float: left;
  width: 2.3rem;
  height: 2.3rem;
  border: 1px solid ${Colors.greyMiddle};
  margin: 0.3rem 0.6rem 0.3rem ${Spacing.MarginAroundFormComponents};
  font-size: 1.5rem;
  text-align: center;
  vertical-align: middle;
  cursor: default;
  color: ${Colors.greyMiddle};
  background-color: ${Colors.white};
  &.active {
    color: ${Colors.black};
    background-color: ${Colors.yellow};
  }
`

export const MenuAllergenIndicator: React.FC<IProps> = ({
  selected,
  onToggle,
}) => {
  const { data, loading } = useQMenuAllergens(
    {},
    { fetchPolicy: 'cache-first' },
  )
  if (loading || !data) {
    return null
  }
  return (
    <>
      {data.allergens.map(allergen => {
        return (
          <AllergenBox
            key={allergen.enum}
            title={allergen.code}
            className={cn({ active: selected.includes(allergen.enum) })}
            onClick={onToggle && (() => onToggle(allergen.enum))}
          >
            {allergen.category}
          </AllergenBox>
        )
      })}
    </>
  )
}
