import { useXFieldState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

type TProps = NoChildren & {
  fieldName: XFieldName
  className?: string
}

export const FormDisplayValue: React.FC<TProps> = ({
  fieldName,
  className,
}) => {
  const field = useXFieldState<string>(fieldName)
  return useObserver(() => <span className={className}>{field.value}</span>)
}
