import { ErrorOutline } from '@material-ui/icons'
import { useXField } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { AllergenEnum } from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { MenuAllergenIndicator } from '../atoms/MenuAllergenIndicator'

interface IProps {}

export const StockAllergenField: React.FC<IProps> = () => {
  const field = useXField<RoA<AllergenEnum>>('allergens')

  const onToggle = React.useCallback(
    (allergen: AllergenEnum) => {
      const current = field.value
      if (current.includes(allergen)) {
        field.setValue(RM.without([allergen], current))
      } else {
        field.setValue([...current, allergen])
      }
    },
    [field],
  )

  return useObserver(() => (
    <IconLine icon={ErrorOutline} labelWidth={7}>
      <MenuAllergenIndicator selected={field.value} onToggle={onToggle} />
    </IconLine>
  ))
}
