import 'react-image-crop/dist/ReactCrop.css'

import { Trans } from '@lingui/macro'
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core'
import React from 'react'
import ReactCrop, { Crop } from 'react-image-crop'

import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

type TProps = NoChildren & {
  image: File
  onConfirm(blob: Blob): void
  onCancel(): void
}

export const ImageCropModal: React.FC<TProps> = ({
  image,
  onConfirm,
  onCancel,
}) => {
  const [crop, setCrop] = React.useState<Crop>({
    unit: '%',
    width: 50,
    aspect: 1,
  })

  const onChange = React.useCallback((crop: Crop) => {
    setCrop(crop)
  }, [])

  const imageRef = React.useRef<HTMLImageElement>()

  const onConfirmClick = React.useCallback(() => {
    if (imageRef.current) {
      // using "any" here as library will always fill up whole crop object
      getCroppedBlob(imageRef.current, crop as any).then(onConfirm)
    }
  }, [crop, onConfirm])

  const onImageLoaded = React.useCallback((image: HTMLImageElement) => {
    imageRef.current = image
    // * https://github.com/DominicTobias/react-image-crop/issues/306
    // const cropSize = Math.min(image.width, image.height) / 2
    // setCrop({
    //   aspect: 1,
    //   unit: 'px',
    //   width: cropSize,
    //   x: image.width / 2 - cropSize / 2,
    //   y: image.height / 2 - cropSize / 2,
    // })
    // return false
  }, [])

  const imageSource = React.useMemo(() => URL.createObjectURL(image), [image])

  React.useEffect(() => {
    return () => {
      URL.revokeObjectURL(imageSource)
    }
  }, [imageSource])

  React.useEffect(() => {
    return () => {
      imageRef.current = undefined
    }
  }, [])

  return (
    <Dialog open fullWidth>
      <DialogTitle>
        <Trans>Choose the most significant part of the image</Trans>
      </DialogTitle>
      <DialogContent>
        <ReactCrop
          src={imageSource}
          crop={crop}
          onChange={onChange}
          onImageLoaded={onImageLoaded}
          keepSelection
          ruleOfThirds
          imageStyle={{ width: '100%' }}
        />
      </DialogContent>
      <DialogActions>
        <TouchButton onClick={onCancel}>
          <Trans>Cancel</Trans>
        </TouchButton>
        <TouchButton onClick={onConfirmClick} color={Colors.green}>
          <Trans>Confirm</Trans>
        </TouchButton>
      </DialogActions>
    </Dialog>
  )
}

function getCroppedBlob(
  image: HTMLImageElement,
  crop: Required<Crop>,
): Promise<Blob> {
  const canvas = document.createElement('canvas')
  const scaleX = image.naturalWidth / image.width
  const scaleY = image.naturalHeight / image.height
  canvas.width = crop.width
  canvas.height = crop.height

  const ctx = canvas.getContext('2d')!
  ctx.drawImage(
    image,
    crop.x * scaleX,
    crop.y * scaleY,
    crop.width * scaleX,
    crop.height * scaleY,
    0,
    0,
    crop.width,
    crop.height,
  )

  return new Promise((resolve, reject) => {
    canvas.toBlob(blob => {
      if (!blob) {
        reject(new Error('Canvas is empty'))
        return
      }
      resolve(blob)
    }, 'image/jpeg')
  })
}
