import { Trans } from '@lingui/macro'
import { useAutorun } from '@speedlo/hooks'
import { useXField } from '@speedlo/xform'
import _ from 'lodash'
import { observer, useLocalStore } from 'mobx-react-lite'
import React from 'react'

import { SelectControlFlat } from '../../../atoms/SelectControlFlat'
import { i18n } from '../../../i18n'
import { CategoryRequirementControl } from '../molecules/CategoryRequirementControl'

interface IRequirement {
  label: string
  min?: number
  max?: number
}

export const getRulesMap = (): Record<string, IRequirement> => ({
  noLimit: {
    label: i18n.t`No limitation`,
    min: 0,
    max: 0,
  },
  optional: {
    label: i18n.t`One at most`,
    min: 0,
    max: 1,
  },
  required: {
    label: i18n.t`One at least`,
    min: 1,
    max: 0,
  },
  onlyOne: {
    label: i18n.t`Exactly one`,
    min: 1,
    max: 1,
  },
  custom: {
    label: i18n.t`Custom range`,
  },
})

type TRule = keyof ReturnType<typeof getRulesMap>

export const CategoryRequirementsForm = observer(() => {
  const ruleMap = React.useMemo(getRulesMap, [])

  const state = useLocalStore(() => ({
    currentRule: 'noLimit' as TRule,
  }))

  const setRule = (rule: TRule) => {
    state.currentRule = rule
  }

  const minCount = useXField<number>('minCount')
  const maxCount = useXField<number>('maxCount')

  useAutorun(() => {
    if (state.currentRule !== 'custom') {
      const found = _.findKey(
        ruleMap,
        ({ min, max }) => min === minCount.value && max === maxCount.value,
      )
      if (found) {
        setRule(found)
      }
    }
  })

  return (
    <>
      <SelectControlFlat<TRule>
        items={Object.keys(ruleMap) as RoA<TRule>}
        formatItem={req => ruleMap[req].label}
        onSingleSelection={selectedRule => {
          if (selectedRule) {
            setRule(selectedRule)
            const rule = ruleMap[selectedRule]
            minCount.setValue(rule.min || 0)
            maxCount.setValue(rule.max || 0)
          }
        }}
        selectedItem={state.currentRule}
      />
      <CategoryRequirementControl
        id="minCount"
        count={minCount.value}
        label={<Trans>Minimum</Trans>}
        enabled={state.currentRule === 'custom'}
        onChange={minCount.setValue}
      />
      <CategoryRequirementControl
        id="maxCount"
        count={maxCount.value}
        label={<Trans>Maximum</Trans>}
        enabled={state.currentRule === 'custom'}
        onChange={maxCount.setValue}
      />
    </>
  )
})
