import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/generated'
import { useMenuItemType } from '../hooks/useMenuItemType'

interface IProps {
  isMain?: boolean
  isSideDish?: boolean
  isCover?: boolean
  children: ReactNode | ReactNode[]
}

export const IsMenuItemType: React.FC<IProps> = ({
  isMain,
  isSideDish,
  isCover,
  children,
}) => {
  const itemType = useMenuItemType()
  if (
    (isMain === true && itemType === WareCategoryTypeEnum.Main) ||
    (isSideDish === true && itemType === WareCategoryTypeEnum.SideDish) ||
    (isCover === true && itemType === WareCategoryTypeEnum.Cover)
  ) {
    return <>{children}</>
  }
  return null
}
