import '@speedlo/assets/Flaticon.css'

import styled from 'styled-components/macro'

import { Spacing } from '../styling'

interface IProps {
  pictogramId: string
  fontSize?: number
  fontSizeUnit?: string
}

const getFontSize = ({
  fontSize = 2,
  fontSizeUnit = Spacing.DefaultSizeUnit,
}: IProps) => `${fontSize}${fontSizeUnit}`

export const PictogramDisplay = styled.div.attrs<IProps>(({ pictogramId }) => ({
  className: getPictogramClassName(pictogramId),
}))<IProps>`
  &:before {
    font-size: ${getFontSize} !important;
    margin-left: 0.5rem !important;
  }
`

export function getPictogramClassName(pictogramId: string) {
  return `flaticon-${pictogramId}`
}
