import { SelectAll } from '@material-ui/icons'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormDropdownSingle } from '../../../components/forms/FormDropdownSingle'
import {
  QMenuWareUnitsWareUnits,
  useQMenuWareUnits,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { i18n } from '../../../i18n'
import { RM } from '../../../tools/ramda'

type TWareUnits = QMenuWareUnitsWareUnits

export const StockWareUnitField: React.FC<NoChildren> = () => {
  const { data, loading } = useQMenuWareUnits()
  return (
    <IconLine icon={SelectAll} labelWidth={7}>
      <FormDropdownSingle<TWareUnits>
        fieldName="wareUnit"
        isLoading={loading}
        options={data ? data.wareUnits : ARRAY_EMPTY}
        getOptionLabel={RM.prop('code')}
        getOptionValue={RM.prop('enum')}
        placeholder={i18n.t`Choose unit`}
        width={12}
      />
    </IconLine>
  )
}
