import { useObjectValidation } from '@speedlo/xform'
import React from 'react'

import {
  FieldStateEnum,
  QMenuStockDefaultsDefaults,
  QMenuStockFormStockItem,
  useLazyQMenuStockDefaults,
  useLazyQMenuStockForm,
  useMMenuStockCreate,
  useMMenuStockUpdate,
} from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { useMenuModel } from '../hooks/useMenu'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { ROUTE_MENU_STOCK } from '../menu.routes'
import { TStockFormValues } from '../menu.types'
import { MenuFormLoader } from '../molecules/MenuFormLoader'

interface IProps {
  stockId: ID
  isCreate: boolean
  children: ReactNode
}

export const StockFormLoader: React.FC<IProps> = ({
  stockId,
  isCreate,
  children,
}) => {
  const menuModel = useMenuModel()
  const { redirectToStockList } = useMenuRouting()

  const validationSchema = useObjectValidation(yup => ({
    code: yup.string().required(),
    nameLabel: yup.string().required(),
    wareUnit: yup.string().required(),
    ingredients: yup.array().of(
      yup.object({
        quantity: yup.string().required(),
      }),
    ),
  }))

  const [queryDefaults] = useLazyQMenuStockDefaults()
  const loadCreateDefaults = React.useCallback(
    async (values: TStockFormValues) => {
      const result = await queryDefaults({ branchId: menuModel.branchId })
      if (result === null) {
        return values
      }
      return {
        ...values,
        ...normalizeLoadedDefaults(result.defaults),
      }
    },
    [menuModel, queryDefaults],
  )

  const [queryStock] = useLazyQMenuStockForm()
  const loadUpdateValues = React.useCallback(
    async (values: TStockFormValues) => {
      const result = await queryStock({ stockId }, { fetchPolicy: 'no-cache' })
      if (result === null) {
        redirectToStockList()
        return values
      }
      return {
        ...normalizeUpdateValues(result.stockItem),
      }
    },
    [queryStock, redirectToStockList, stockId],
  )

  const [createStock] = useMMenuStockCreate()
  const onCreate = React.useCallback(
    (values: TStockFormValues) =>
      createStock({
        stockItem: {
          companyBranch: menuModel.branchId,
          ...getWriteVariables(values),
        },
      }),
    [createStock, menuModel],
  )

  const [updateStock] = useMMenuStockUpdate()
  const onUpdate = React.useCallback(
    (values: TStockFormValues) =>
      updateStock({
        stockId,
        stockItem: getWriteVariables(values),
      }),
    [stockId, updateStock],
  )

  return (
    <MenuFormLoader<TStockFormValues>
      isCreate={isCreate}
      onCreate={onCreate}
      onUpdate={onUpdate}
      initialValues={getDefaults()}
      loadCreateDefaults={loadCreateDefaults}
      loadUpdateValues={loadUpdateValues}
      validationSchema={validationSchema}
      successRedirectPath={ROUTE_MENU_STOCK}
    >
      {children}
    </MenuFormLoader>
  )
}

function getDefaults(): TStockFormValues {
  return {
    id: 'NEW',
    internalNote: '',
    nameLabel: '',
    code: '',
    plu: '',
    note: '',
    wareUnit: null,
    quantity: 0,
    allergens: [],
    allowNegative: FieldStateEnum.Default,
    allowNegativeDefault: FieldStateEnum.Default,
  }
}

function normalizeLoadedDefaults(
  defaults: QMenuStockDefaultsDefaults,
): Partial<TStockFormValues> {
  return {
    allowNegativeDefault: getAllowNegativeDefault(defaults.allowNegative),
  }
}

function normalizeUpdateValues(
  stockItem: QMenuStockFormStockItem,
): TStockFormValues {
  return {
    id: stockItem.id,
    internalNote: 'TBD',
    nameLabel: stockItem.nameLabel,
    code: stockItem.code,
    note: stockItem.note,
    plu: stockItem.plu || '',
    wareUnit: stockItem.wareUnit.enum,
    quantity: stockItem.quantity,
    allergens: stockItem.allergens.map(RM.prop('enum')),
    allowNegative: stockItem.allowNegative,
    allowNegativeDefault: getAllowNegativeDefault(
      stockItem.companyBranch.allowNegative,
    ),
  }
}

function getAllowNegativeDefault(isEnabled: boolean) {
  return isEnabled ? FieldStateEnum.Default : FieldStateEnum.Disabled
}

function getWriteVariables(formValues: TStockFormValues) {
  return {
    code: formValues.code,
    label: formValues.nameLabel,
    note: formValues.note,
    plu: formValues.plu,
    allergens: formValues.allergens,
    wareUnit: formValues.wareUnit!,
    allowNegative: formValues.allowNegative,
  }
}
