import React from 'react'

type TOptions<TSelectOption> = {
  options: RoA<TSelectOption> | null
  selected: MaybeID
  onSelectionChange: (selected: MaybeID) => void
  getOptionValue(option: TSelectOption): string
}

export function useDropdownSingle<TSelectOption>({
  options,
  selected,
  onSelectionChange,
  getOptionValue,
}: TOptions<TSelectOption>) {
  const value = React.useMemo(() => {
    if (selected === null) {
      return null
    }
    return options === null
      ? null
      : options.find(option => getOptionValue(option) === selected) || null
  }, [selected, options, getOptionValue])

  const onChange = (option: unknown) => {
    onSelectionChange(option ? getOptionValue(option as TSelectOption) : null)
  }

  return { value, onChange, getOptionValue }
}
