import { Trans } from '@lingui/macro'
import React from 'react'

import { FormSubmitButton } from '../../../components/forms/FormSubmitButton'
import { useCanMutate } from '../../../hooks/useAccessGuard'

interface IProps {
  isCreate: boolean
  createMutation: string
  updateMutation: string
}

export const MenuSubmitButton: React.FC<IProps> = ({
  isCreate,
  createMutation,
  updateMutation,
}) => {
  const canSubmit = useCanMutate(isCreate ? createMutation : updateMutation)
  if (!canSubmit) {
    return null
  }

  return (
    <FormSubmitButton>
      {isCreate ? <Trans>Create</Trans> : <Trans>Save</Trans>}
    </FormSubmitButton>
  )
}
