import { Trans } from '@lingui/macro'
import { XFieldName } from '@speedlo/xform'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormInput } from '../../../components/forms/FormInput'
import { MenuTranslationInput } from '../atoms/MenuTranslationInput'

interface IProps {
  fieldName?: XFieldName
  label?: string
  placeholder?: string
  localized?: boolean
}

export const MenuInternalNoteInput: React.FC<IProps> = ({
  fieldName = 'internalNote',
  label = <Trans>Internal note</Trans>,
  placeholder,
  localized = false,
}) => {
  const props = {
    fieldName,
    placeholder,
    fullWidth: true,
  }
  return (
    <IconLine label={label} labelWidth={7}>
      {localized ? (
        <MenuTranslationInput {...props} />
      ) : (
        <FormInput {...props} />
      )}
    </IconLine>
  )
}
