import React from 'react'

import { FormGrid } from '../../../atoms/FormGrid'
import { IconLine } from '../../../atoms/IconLine'
import { FormDeleteButton } from '../../../components/forms/FormDeleteButton'
import { useMMenuRecipeDelete } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { Spacing } from '../../../styling'
import { FormLayoutGrid } from '../atoms/FormLayoutGrid'
import { IsMenuItemType } from '../atoms/IsMenuItemType'
import { MenuCodeInput } from '../molecules/MenuCodeInput'
import { MenuIdExternalSystem } from '../molecules/MenuIdExternalSystem'
import { MenuInternalNoteInput } from '../molecules/MenuInternalNoteField'
import { MenuLanguageSwitch } from '../molecules/MenuLanguageSwitch'
import { MenuNameInput } from '../molecules/MenuNameInput'
import { MenuVisibilityField } from '../molecules/MenuVisibilityField'
import { RecipeAllergenDisplay } from '../molecules/RecipeAllergenDisplay'
import { RecipeDescriptionInput } from '../molecules/RecipeDescriptionInput'
import { RecipeImage } from '../molecules/RecipeImage'

interface IProps {
  isCreate: boolean
}

export const RecipeFormPrimary: React.FC<IProps> = ({ isCreate }) => {
  const [deleteRecipe] = useMMenuRecipeDelete()

  return (
    <FormLayoutGrid
      primaryColumnWidth="minmax(24rem, 54rem)"
      secondaryColumnWidth="minmax(12rem, 30rem)"
      contentPrimary={
        <FormGrid>
          <MenuCodeInput placeholder={i18n.t`Recipe code`} />
          <MenuNameInput placeholder={i18n.t`Recipe name`} localized />
          <IsMenuItemType isMain>
            <RecipeDescriptionInput />
          </IsMenuItemType>
          <MenuInternalNoteInput placeholder={i18n.t`Recipe internal note`} />
          {!isCreate && <MenuIdExternalSystem />}
          <IsMenuItemType isMain>
            <RecipeAllergenDisplay />
          </IsMenuItemType>
          <IconLine labelWidth={Spacing.DefaultLabelWidth}>
            <MenuVisibilityField label={i18n.t`To show this recipe`} />
          </IconLine>
        </FormGrid>
      }
      contentSecondary={<RecipeImage />}
      contentTopRight={<MenuLanguageSwitch />}
      contentBottomRight={
        <FormDeleteButton
          mutationName="destroyRecipe"
          confirmText={i18n.t`Remove this recipe?`}
          onDelete={recipeId => deleteRecipe({ recipeId })}
        />
      }
    />
  )
}
