import React from 'react'
import {
  arrayMove,
  SortableContainer,
  SortableElement,
  SortEndHandler,
} from 'react-sortable-hoc'
import { TableRowProps } from 'react-virtualized'

import { DataTable, IDataTableProps } from '../atoms/DataTable'

interface ISortResult<T> {
  item: T
  items: RoA<T>
  offset: number
  oldIndex: number
  newIndex: number
}

export type TSortHandler<T> = (
  result: ISortResult<T>,
) => Promise<boolean> | void

interface IProps<T> extends IDataTableProps<T> {
  items: RoA<T>
  onSort?: TSortHandler<T>
}

interface IState<T> {
  sortedItems: RoA<T>
}

const SortableTableRowRenderer = SortableElement(
  ({ columns, style, className }: TableRowProps) => (
    <div style={style} className={className}>
      {columns}
    </div>
  ),
)

const SortableDataTableContainer = SortableContainer(DataTable)

export class SortableDataTable<T> extends React.PureComponent<
  IProps<T>,
  IState<T>
> {
  state = { sortedItems: this.props.items }
  static getDerivedStateFromProps = (nextProps: IProps<{}>) => {
    return {
      sortedItems: nextProps.items,
    }
  }
  rowRenderer = rowProps => {
    return <SortableTableRowRenderer {...rowProps} />
  }
  onHandleSorting: SortEndHandler = async ({ newIndex, oldIndex }) => {
    if (newIndex === oldIndex) {
      return
    }
    const currentItems = [...this.state.sortedItems]
    const sortedItems = arrayMove(
      this.state.sortedItems as T[],
      oldIndex,
      newIndex,
    )

    this.setState({ sortedItems })

    if (this.props.onSort) {
      const sortSuccess = this.props.onSort({
        item: currentItems[oldIndex],
        items: sortedItems,
        offset: newIndex - oldIndex,
        newIndex,
        oldIndex,
      })
      if (sortSuccess && !(await sortSuccess)) {
        this.setState({
          sortedItems: currentItems,
        })
      }
    }
  }
  render() {
    const { items, ...props } = this.props
    return (
      <SortableDataTableContainer
        lockAxis="y"
        useDragHandle={true}
        onSortEnd={this.onHandleSorting}
        rowRenderer={this.rowRenderer}
        {...props}
        items={this.state.sortedItems}
      />
    )
  }
}
