import React, { Fragment } from 'react'
import styled from 'styled-components/macro'

import { FlexDiv } from '../../../atoms/FlexDiv'
import { Headline } from '../../../atoms/Headline'
import { MarginBox } from '../../../atoms/MarginBox'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { FormDisplayValue } from '../../../components/forms/FormDisplayValue'
import { IsMenuItemType } from '../atoms/IsMenuItemType'
import { usePriceType } from '../hooks/usePriceType'
import { MenuPriceField } from '../molecules/MenuPriceInput'
import { RecipeVatSelectField } from '../molecules/RecipeVatSelectField'
import { RecipeAssociatedView } from './RecipeAssociatedView'

interface IProps {}

const FormPricingCurrencyCode = styled(FormDisplayValue)`
  padding: 0.5rem;
`

export const RecipeFormPricing: React.FC<IProps> = () => {
  const { priceTypes, priceTypeLabels } = usePriceType()
  return (
    <>
      {priceTypes.map(priceType => {
        const baseName = ['prices', priceType]
        return (
          <Fragment key={priceType}>
            <Headline>{priceTypeLabels[priceType]}</Headline>
            <FlexDiv>
              <MenuPriceField fieldName={[...baseName, 'value']} />
              <FormPricingCurrencyCode
                fieldName={[...baseName, 'currencyCode']}
              />
              <MarginBox left={5}>
                <RecipeVatSelectField fieldName={[...baseName, 'vatId']} />
              </MarginBox>
            </FlexDiv>
            <VerticalGap />
            <IsMenuItemType isMain>
              <RecipeAssociatedView priceType={priceType} />
              <VerticalGap />
            </IsMenuItemType>
          </Fragment>
        )
      })}
    </>
  )
}
