import { pictograms as availablePictograms } from '@speedlo/assets'
import { useXField } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { PictogramDisplay } from '../../../atoms/PictogramDisplay'
import { SelectControl } from '../../../atoms/SelectControl'
import { RM } from '../../../tools/ramda'
import { TCategoryFormValues } from '../menu.types'

interface IProps {}

interface IFormValue {
  pictogram: string
}

export const CategoryPictogramSelect: React.FC<IProps> = () => {
  const field = useXField<TCategoryFormValues['pictogram']>('pictogram')

  return useObserver(() => (
    <SelectControl<IFormValue>
      value={field.value ? { pictogram: field.value } : null}
      onChange={value => {
        if (value) {
          field.setValue((value as IFormValue).pictogram)
        }
      }}
      options={availablePictograms.map(pictogram => ({ pictogram }))}
      getOptionValue={RM.prop('pictogram')}
      formatOptionLabel={({ pictogram }) => (
        <PictogramDisplay pictogramId={pictogram} fontSize={2} />
      )}
      components={{
        SingleValue: ({ data }) => (
          <PictogramDisplay pictogramId={data.pictogram} fontSize={4} />
        ),
      }}
      styles={{
        container: base => ({ ...base, width: '8rem' }),
        menuList: base => ({ ...base, maxHeight: '35rem' }),
      }}
      isClearable={false}
      isSearchable={false}
      minWidth={8}
    />
  ))
}
