import { disableBatching } from '@speedlo/graphql'
import { useObjectValidation } from '@speedlo/xform'
import React from 'react'

import {
  InputRecipeImage,
  QMenuRecipeDefaultsDefaults,
  QMenuRecipeFormRecipe,
  useLazyQMenuRecipeDefaults,
  useLazyQMenuRecipeForm,
  useMMenuRecipeCreate,
  useMMenuRecipeUpdate,
  WarePriceTypeEnum,
} from '../../../graph/generated'
import {
  fieldIsRequiredMsg,
  validateAsNumberField,
} from '../../../helpers/form'
import { formatNumber, formatPrice } from '../../../helpers/formats'
import { parseNumber } from '../../../helpers/numeral'
import { i18n } from '../../../i18n'
import { RM } from '../../../tools/ramda'
import { useMenuModel } from '../hooks/useMenu'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { priceTypes, usePriceType } from '../hooks/usePriceType'
import { ROUTE_MENU_RECIPE } from '../menu.routes'
import { TRecipeFormValues, TRecipeType } from '../menu.types'
import { MenuFormLoader } from '../molecules/MenuFormLoader'
import { RecipeTranslationLoader } from '../molecules/RecipeTranslationLoader'

interface IProps {
  isCreate: boolean
  recipeId: ID
  recipeType: TRecipeType
  children: ReactNode
}

type FormValues = TRecipeFormValues

export const RecipeFormLoader: React.FC<IProps> = ({
  isCreate,
  recipeId,
  recipeType,
  children,
}) => {
  const menuModel = useMenuModel()
  const { redirectToRecipeList } = useMenuRouting()
  const { priceTypeLabels } = usePriceType()

  const validationSchema = useObjectValidation(yup => ({
    nameLabel: yup
      .string()
      .required(fieldIsRequiredMsg(i18n.t`Recipe name in default language`)),
    code: yup.string().required(fieldIsRequiredMsg(i18n.t`Recipe code`)),
    prices: yup.object(
      mapPriceTypes(type => {
        const priceType = priceTypeLabels[type].toLocaleLowerCase()
        const fieldDescription = i18n.t`Recipe price for ${priceType}`
        return yup.object({
          value: validateAsNumberField(fieldDescription),
        })
      }),
    ),
  }))

  const [queryDefaults] = useLazyQMenuRecipeDefaults()
  const loadCreateDefaults = React.useCallback(async () => {
    const result = await queryDefaults(
      {
        branchId: menuModel.branchId,
      },
      { fetchPolicy: 'cache-first' },
    )
    if (result !== null) {
      return normalizeLoadedDefaults(result.defaults)
    }
    return
  }, [menuModel, queryDefaults])

  const [queryRecipe] = useLazyQMenuRecipeForm()
  const loadUpdateValues = React.useCallback(async () => {
    const result = await queryRecipe(
      { recipeId: recipeId!, language: menuModel.defaultLanguage },
      { fetchPolicy: 'no-cache' },
    )
    if (result === null) {
      redirectToRecipeList()
      return
    }
    return normalizeUpdateValues(result.recipe)
  }, [menuModel.defaultLanguage, queryRecipe, recipeId, redirectToRecipeList])

  const [updateRecipe] = useMMenuRecipeUpdate()
  const executeUpdate = React.useCallback(
    (recipeId: ID, values: FormValues) =>
      updateRecipe(
        {
          recipeId,
          recipe: {
            ...getWriteVariables(values),
            translations: values.translations,
            image: getImageForUpdate(values),
          },
        },
        disableBatching, // has to be disabled because of file upload
      ),
    [updateRecipe],
  )

  const onUpdate = React.useCallback(
    (values: FormValues) => executeUpdate(recipeId, values),
    [executeUpdate, recipeId],
  )

  const [createRecipe] = useMMenuRecipeCreate()
  const onCreate = React.useCallback(
    async (values: FormValues) => {
      const result = await createRecipe(
        {
          recipe: {
            ...getWriteVariables(values),
            companyBranch: menuModel.branchId,
            wareCategoryType: recipeType,
            image: values.image,
          },
        },
        disableBatching, // has to be disabled because of file upload
      )
      if (result && values.translations && values.translations.length > 0) {
        const recipeId = result.recipe.id
        executeUpdate(recipeId, values)
      }
    },
    [createRecipe, executeUpdate, menuModel, recipeType],
  )

  return (
    <MenuFormLoader<TRecipeFormValues>
      isCreate={isCreate}
      onCreate={onCreate}
      onUpdate={onUpdate}
      initialValues={getDefaults(recipeType)}
      loadCreateDefaults={loadCreateDefaults}
      loadUpdateValues={loadUpdateValues}
      validationSchema={validationSchema}
      successRedirectPath={ROUTE_MENU_RECIPE}
    >
      <>
        {children}
        <RecipeTranslationLoader recipeId={recipeId} isCreate={isCreate} />
      </>
    </MenuFormLoader>
  )
}

function getWriteVariables(formValues: FormValues) {
  return {
    code: formValues.code,
    nameLabel: formValues.nameLabel || '',
    internalNote: formValues.internalNote,
    descriptionLabel: formValues.descriptionLabel,
    enabled: formValues.visible,
    prices: Object.keys(formValues.prices).map(priceType => ({
      warePriceType: (priceType as unknown) as WarePriceTypeEnum,
      price: { value: parseNumber(formValues.prices[priceType].value) },
      vat: formValues.prices[priceType].vatId,
    })),
    sideDishCategories: formValues.sideDishes.map(RM.prop('id')),
    wareCategories: formValues.categories.map(RM.prop('id')),
    associatedRecipes: formValues.associatedRecipes.map(
      RM.pick(['associatedRecipe', 'warePriceType']),
    ),
    ingredients: formValues.ingredients.map(({ stockItem, quantity }) => {
      return { stockItem, quantity: parseNumber(quantity) }
    }),
  }
}

function getImageForUpdate({ image }: FormValues): Maybe<InputRecipeImage> {
  if (typeof image === 'string') {
    // image did not change, do nothing
    return null
  }
  if (image === null) {
    return {
      delete: true,
    }
  }
  return {
    file: image,
    delete: false,
  }
}

function mapPriceTypes<T>(acc: (priceType: WarePriceTypeEnum) => T) {
  return priceTypes.reduce((result, priceType) => {
    result[priceType] = acc(priceType)
    return result
  }, {}) as { [priceType in WarePriceTypeEnum]: T }
}

function getDefaults(recipeType: TRecipeType): FormValues {
  return {
    id: 'NEW',
    itemType: recipeType,
    visible: true,
    code: '',
    nameLabel: '',
    internalNote: '',
    internalCode: '',
    descriptionLabel: '',
    image: null,
    prices: mapPriceTypes(() => ({
      value: '0',
      vatId: null,
      currencyCode: '',
    })),
    associatedRecipes: [],
    ingredients: [],
    sideDishes: [],
    categories: [],
    translations: [],
  }
}

function normalizeLoadedDefaults(
  defaults: QMenuRecipeDefaultsDefaults,
): Partial<FormValues> {
  const vats = defaults.vats.reduce((acc, { vatId, priceType }) => {
    acc[priceType.enum] = vatId
    return acc
  }, {})
  return {
    prices: mapPriceTypes(priceType => ({
      value: '0',
      vatId: vats[priceType],
      currencyCode: defaults.currency.code,
    })),
  }
}

function normalizeUpdateValues(
  recipeSource: QMenuRecipeFormRecipe,
): Partial<FormValues> {
  return {
    ...recipeSource,
    itemType: recipeSource.recipeType.enum,
    internalNote: recipeSource.internalNote || '',
    descriptionLabel: recipeSource.descriptionLabel || '',
    associatedRecipes: recipeSource.sourceAssociatedRecipes.map(
      ({ priceType, recipe }) => ({
        warePriceType: priceType.enum,
        nameLabel: recipe.nameLabel!,
        associatedRecipe: recipe.id,
        formattedPrice: '?',
      }),
    ),
    ingredients: recipeSource.sourceIngredients.map(
      ({ quantity, stockItem: { id, label, allergens, wareUnit } }) => ({
        stockItem: id,
        label,
        quantity: formatNumber(quantity),
        unitLabel: wareUnit.code,
        allergens: allergens.map(RM.prop('enum')),
      }),
    ),
    prices: mapPriceTypes(priceType => {
      const sourcePrice = recipeSource.sourcePrices.find(
        RM.pathEq(['priceType', 'enum'], priceType),
      )
      if (!sourcePrice) {
        return {
          value: '0',
          vatId: null,
          currencyCode: '',
        }
      }
      return {
        value: formatPrice(sourcePrice.price.value),
        vatId: sourcePrice.vat.id,
        currencyCode: sourcePrice.price.currency.code,
      }
    }),
  }
}
