import { useXField, useXFormState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { SelectControl, TSelectControlProps } from '../../atoms/SelectControl'
import { useDropdownFilter } from '../../hooks/useDropdownFilter'
import { useDropdownMulti } from '../../hooks/useDropdownMulti'
import { idProp } from '../../tools/ramda'

type TFormDropdownMultiProps<OptionType> = Omit<
  TSelectControlProps<OptionType>,
  'value' | 'onChange' | 'isMulti'
> & {
  fieldName: XFieldName
  options: RoA<OptionType>
  getOptionFilter?(option: OptionType): string
}

export const FormDropdownMulti = <TOption extends any>({
  fieldName,
  getOptionValue = idProp as any,
  getOptionFilter,
  ...selectProps
}: TFormDropdownMultiProps<TOption>) => {
  const formState = useXFormState()
  const field = useXField<RoA<ID>>(fieldName)

  return useObserver(function useBody() {
    const filterProps = useDropdownFilter({ getOptionFilter })
    const dropdownProps = useDropdownMulti({
      ...selectProps,
      onSelectionChange: field.setValue,
      selected: field.value,
      getOptionValue,
    })

    return (
      <SelectControl<TOption>
        {...dropdownProps}
        {...filterProps}
        {...selectProps}
        onFocus={() => field.setTouched()}
        invalid={field.touched && field.hasError}
        isDisabled={formState.isSubmitting}
        getOptionValue={getOptionValue}
      />
    )
  })
}
