import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import {
  ROUTE_MENU,
  ROUTE_MENU_CATEGORY,
  ROUTE_MENU_RECIPE,
  ROUTE_MENU_STOCK,
} from '../menu.routes'
import { MenuCategoryListPage } from '../pages/MenuCategoryListPage'
import { MenuCategoryPage } from '../pages/MenuCategoryPage'
import { MenuRecipeListPage } from '../pages/MenuRecipeListPage'
import { MenuRecipePage } from '../pages/MenuRecipePage'
import { MenuStockListPage } from '../pages/MenuStockListPage'
import { MenuStockPage } from '../pages/MenuStockPage'

interface IProps {}

export const MenuRouting: React.FC<IProps> = () => (
  <Switch>
    <SecureRoute
      path={`${ROUTE_MENU_CATEGORY}/:categoryId`}
      component={MenuCategoryPage}
    />
    <SecureRoute path={ROUTE_MENU_CATEGORY} component={MenuCategoryListPage} />
    <SecureRoute
      path={`${ROUTE_MENU_RECIPE}/:recipeId`}
      component={MenuRecipePage}
    />
    <SecureRoute path={ROUTE_MENU_RECIPE} component={MenuRecipeListPage} />
    <SecureRoute
      path={`${ROUTE_MENU_STOCK}/:stockId`}
      component={MenuStockPage}
    />
    <SecureRoute path={ROUTE_MENU_STOCK} component={MenuStockListPage} />
    <Redirect to={ROUTE_MENU_CATEGORY} from={ROUTE_MENU} />
    <Route component={NotFoundPage} />
  </Switch>
)

export default MenuRouting
