import { useObjectValidation } from '@speedlo/xform'
import React from 'react'

import {
  useLazyQMenuCategoryForm,
  useMMenuCategoryCreate,
  useMMenuCategoryUpdate,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { idProp } from '../../../tools/ramda'
import { useMenuModel } from '../hooks/useMenu'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { ROUTE_MENU_CATEGORY } from '../menu.routes'
import { TCategoryFormValues } from '../menu.types'
import { CategoryTranslationLoader } from '../molecules/CategoryTranslationLoader'
import { MenuFormLoader } from '../molecules/MenuFormLoader'

interface IProps {
  isCreate: boolean
  categoryId: ID
  categoryType: WareCategoryTypeEnum
  children: ReactNode
}

export const CategoryFormLoader: React.FC<IProps> = ({
  isCreate,
  categoryId,
  categoryType,
  children,
}) => {
  const menuModel = useMenuModel()
  const { redirectToCategoryList } = useMenuRouting()

  const validationSchema = useObjectValidation(yup => ({
    nameLabel: yup
      .string()
      .required(i18n.t`Category name in default language must be entered.`),
  }))

  const [queryCategory] = useLazyQMenuCategoryForm()
  const loadUpdateValues = React.useCallback(
    async (values: TCategoryFormValues) => {
      const result = await queryCategory(
        { categoryId: categoryId!, language: menuModel.defaultLanguage },
        { fetchPolicy: 'no-cache' },
      )
      if (result === null) {
        redirectToCategoryList()
        return values
      }
      return {
        ...result.category,
        itemType: categoryType,
        origins: [...result.category.origins],
        color: result.category.color?.id,
      }
    },
    [
      categoryId,
      categoryType,
      menuModel.defaultLanguage,
      queryCategory,
      redirectToCategoryList,
    ],
  )

  const [updateCategory] = useMMenuCategoryUpdate()
  const executeUpdate = React.useCallback(
    (categoryId: ID, values: TCategoryFormValues) =>
      updateCategory({
        categoryId,
        category: {
          ...getWriteVariables(values),
          translations: values.translations,
        },
      }),
    [updateCategory],
  )

  const onUpdate = React.useCallback(
    (values: TCategoryFormValues) => executeUpdate(categoryId, values),
    [categoryId, executeUpdate],
  )

  const [createCategory] = useMMenuCategoryCreate()
  const onCreate = React.useCallback(
    async (values: TCategoryFormValues) => {
      const result = await createCategory({
        category: {
          companyBranch: menuModel.branchId,
          wareCategoryType: categoryType,
          ...getWriteVariables(values),
        },
      })
      if (result && values.translations && values.translations.length > 0) {
        const categoryId = result.createCategory.id
        executeUpdate(categoryId, values)
      }
    },
    [categoryType, createCategory, executeUpdate, menuModel],
  )

  return (
    <MenuFormLoader<TCategoryFormValues>
      isCreate={isCreate}
      onCreate={onCreate}
      onUpdate={onUpdate}
      initialValues={getDefaults(categoryType)}
      loadUpdateValues={loadUpdateValues}
      validationSchema={validationSchema}
      successRedirectPath={ROUTE_MENU_CATEGORY}
    >
      <>
        <CategoryTranslationLoader
          categoryId={categoryId}
          isCreate={isCreate}
        />
        {children}
      </>
    </MenuFormLoader>
  )
}

function getDefaults(categoryType: WareCategoryTypeEnum): TCategoryFormValues {
  return {
    id: 'NEW',
    itemType: categoryType,
    visible: true,
    internalNoteLabel: '',
    pictogram: '',
    nameLabel: '',
    minCount: 0,
    maxCount: 0,
    recipes: [],
    origins: [],
    translations: [],
    color: null,
  }
}

function getWriteVariables(formValues: TCategoryFormValues) {
  return {
    nameLabel: formValues.nameLabel!,
    internalNoteLabel: formValues.internalNoteLabel,
    enabled: formValues.visible,
    pictogram: formValues.pictogram,
    recipes: formValues.recipes.map(idProp),
    minCount: formValues.minCount,
    maxCount: formValues.maxCount,
    orderOriginCategories: formValues.origins,
    wareCategoryColor: formValues.color,
  }
}
