import React from 'react'

import { ARRAY_EMPTY } from '../helpers/form'

type TOptions<TSelectOption> = {
  options: RoA<TSelectOption> | null
  selected: RoA<ID>
  onSelectionChange: (selected: RoA<ID>) => void
  getOptionValue(option: TSelectOption): string
}

export function useDropdownMulti<TSelectOption>({
  options,
  selected,
  onSelectionChange,
  getOptionValue,
}: TOptions<TSelectOption>) {
  const value = React.useMemo(() => {
    if (!Array.isArray(selected)) {
      return null
    }
    return options === null
      ? ARRAY_EMPTY
      : options.filter(option => selected.includes(getOptionValue(option)))
  }, [selected, options, getOptionValue])

  const onChange = (option: unknown) => {
    onSelectionChange(
      option ? (option as TSelectOption[]).map(getOptionValue) : ARRAY_EMPTY,
    )
  }

  return { value, onChange, isMulti: true }
}
