import React from 'react'

import { WarePriceTypeEnum } from '../../../graph/generated'
import { getPriceTypesLabels } from '../menu.labels'

export const priceTypes = [
  WarePriceTypeEnum.Delivery,
  WarePriceTypeEnum.Inhouse,
]

export function usePriceType() {
  const priceTypeLabels = React.useMemo(getPriceTypesLabels, [])
  return {
    priceTypes,
    priceTypeLabels,
  }
}
