import { useXFieldState } from '@speedlo/xform'
import React from 'react'

import { LanguageEnum } from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { useMenuModel } from './useMenu'

const byLanguage = RM.propEq('language')

export interface WithLanguage {
  language: LanguageEnum
}

export interface WithTranslations<T extends WithLanguage> {
  translations: T[]
}

export function useMenuTranslations<T extends WithLanguage>() {
  const menuModel = useMenuModel()
  const field = useXFieldState<WithLanguage[]>('translations')
  const translations = field.value

  const findTranslation = React.useCallback(
    (language: LanguageEnum): T | undefined => {
      return (
        translations &&
        (translations.find(byLanguage(language)) as T | undefined)
      )
    },
    [translations],
  )

  const getTranslation = React.useCallback(
    (language: LanguageEnum): T => {
      return findTranslation(language)!
    },
    [findTranslation],
  )

  const initTranslation = React.useCallback(
    (language: LanguageEnum, translated: Omit<T, 'language'>): T => {
      const translation = findTranslation(language)
      if (!translation) {
        translations.push({ language, ...translated })
        return getTranslation(language)
      }
      return translation
    },
    [findTranslation, getTranslation, translations],
  )

  const hasTranslation = React.useCallback(
    (language: LanguageEnum) => {
      return !!findTranslation(language)
    },
    [findTranslation],
  )

  const shouldLoadTranslation = React.useCallback(() => {
    return !(
      menuModel.isDefaultLanguageSelected ||
      hasTranslation(menuModel.currentLanguage)
    )
  }, [hasTranslation, menuModel])

  return {
    shouldLoadTranslation,
    findTranslation,
    hasTranslation,
    getTranslation,
    initTranslation,
  }
}
