import { Trans } from '@lingui/macro'
import React from 'react'

import { BlockLoader } from '../../../atoms/BlockLoader'
import { Column, ColumnRendererEdit, DataTable } from '../../../atoms/DataTable'
import { Headline } from '../../../atoms/Headline'
import { NoteText } from '../../../atoms/NoteText'
import { useQStockRecipes } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { ROUTE_MENU_RECIPE } from '../menu.routes'

interface IProps {
  stockId: ID
}

export const StockRecipesView: React.FC<IProps> = ({ stockId }) => {
  const { getStash, loading } = useQStockRecipes({
    stockId,
  })

  if (loading) {
    return <BlockLoader />
  }

  const ingredients = getStash(data => data.stockItem.ingredients)

  if (!ingredients || ingredients.length === 0) {
    return (
      <Headline>
        <Trans>No recipes are using stock card</Trans>
      </Headline>
    )
  }

  return (
    <>
      <Headline>
        <Trans>Recipes using this card</Trans>
      </Headline>

      <DataTable
        items={[...ingredients]}
        disableHeader={true}
        fullHeight={true}
      >
        <Column
          dataKey=""
          cellDataGetter={({ rowData }) => ({
            pathname: `${ROUTE_MENU_RECIPE}/${rowData.recipe.id}`,
            state: { recipeType: rowData.recipe.recipeType.enum },
          })}
          cellRenderer={ColumnRendererEdit}
          width={100}
        />
        <Column
          dataKey="recipe"
          width={300}
          label={i18n.t`Name`}
          cellRenderer={({ rowData }) => (
            <>
              {rowData.recipe.nameLabel}
              <NoteText>{rowData.recipe.descriptionLabel}</NoteText>
            </>
          )}
        />
      </DataTable>
    </>
  )
}
