import React from 'react'

import { ColorSelector } from '../../../components/ColorSelector'
import { useQMenuCategoryColors } from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'

export const CategoryColorField: React.FC = () => {
  const { getStash, loading } = useQMenuCategoryColors()
  return (
    <ColorSelector
      fieldName="color"
      isLoading={loading}
      colors={getStash(data => data.colors) ?? ARRAY_EMPTY}
    />
  )
}
