import { useXField, XFieldName } from '@speedlo/xform'
import { IObservableArray } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { DataTableEmptyText } from '../../../atoms/DataTable'
import {
  FMenuCategoryInListFragment,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { idPropEq } from '../../../tools/ramda'
import { CategoryListTable } from '../molecules/CategoryListTable'
import { CategorySuggestionPanel } from './CategorySuggestionPanel'

interface IProps {
  categoryType?: WareCategoryTypeEnum
  addButtonText: string
  noRowsText: string
  fieldName: XFieldName
}

type TCategory = FMenuCategoryInListFragment

export const RecipeCategoryView: React.FC<IProps> = ({
  categoryType,
  addButtonText,
  noRowsText,
  fieldName,
}) => {
  const field = useXField<IObservableArray<TCategory>>(fieldName)
  return useObserver(() => (
    <>
      <CategoryListTable
        categories={Array.from(field.value)}
        showRemoveColumn={true}
        showEditColumn={false}
        showHeader={false}
        noRowsDisplay={<DataTableEmptyText text={noRowsText} />}
        onRemove={categoryId =>
          field.value.remove(field.value.find(idPropEq(categoryId))!)
        }
      />
      <CategorySuggestionPanel
        addButtonText={addButtonText}
        categoryType={categoryType}
        currentCategories={Array.from(field.value)}
        onAdd={addedCategory => field.value.push(addedCategory)}
      />
    </>
  ))
}
