import React from 'react'

import {
  checkColumnAccess,
  Column,
  ColumnRendererEdit,
  ColumnRendererRemove,
  ColumnRendererVisibility,
  ColumnRendererWithNote,
  DataTable,
  DataTableEmptyText,
  IDataTableProps,
} from '../../../atoms/DataTable'
import { DragHandle } from '../../../atoms/DragHandle'
import { FMenuCategoryInListFragment } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import {
  SortableDataTable,
  TSortHandler,
} from '../../../molecules/SortableDataTable'
import { Spacing } from '../../../styling'
import { ColorBadgeMenu } from '../atoms/ColorBadgeMenu'
import { useMenuRouting } from '../hooks/useMenuRouting'

type TCategory = FMenuCategoryInListFragment

interface IProps {
  categories: RoA<TCategory>
  isSortable?: boolean
  isLoading?: boolean
  showEditColumn?: boolean
  showRemoveColumn?: boolean
  showVisibilityColumn?: boolean
  showColor?: boolean
  showHeader?: boolean
  onToggle?(categoryId: ID, visible: boolean): void
  onRemove?(categoryId: ID): void
  onSort?: TSortHandler<TCategory>
  heightStretch?: boolean
  fullHeight?: boolean
  noRowsDisplay?: ReactNode
}

export const CategoryListTable: React.FC<IProps> = ({
  categories,
  children,
  onToggle,
  onRemove,
  onSort,
  showEditColumn = true,
  showRemoveColumn = false,
  showVisibilityColumn = false,
  showColor = false,
  showHeader = true,
  isSortable = false,
  isLoading = false,
  heightStretch = false,
  fullHeight = false,
  noRowsDisplay = <DataTableEmptyText text={i18n.t`No recipes in category`} />,
}) => {
  const props: IDataTableProps<TCategory> = {
    isLoading,
    items: categories,
    disableHeader: !showHeader,
    heightStretch,
    fullHeight,
    noRowsRenderer: () => noRowsDisplay,
  }
  const { getCategoryEditRoute } = useMenuRouting()
  const columns = [
    showEditColumn && (
      <Column
        dataKey="id"
        width={Spacing.ButtonColumnWidthPx}
        minWidth={Spacing.ButtonColumnWidthPx}
        cellRenderer={checkColumnAccess(
          { canQuery: 'wareCategory' },
          ColumnRendererEdit,
        )}
        cellDataGetter={({ rowData }) =>
          getCategoryEditRoute(rowData.id, rowData.categoryType.enum)
        }
      />
    ),
    showRemoveColumn && (
      <Column
        dataKey="id"
        width={Spacing.ButtonColumnWidthPx}
        minWidth={Spacing.ButtonColumnWidthPx}
        cellRenderer={ColumnRendererRemove}
        columnData={{ onRemove }}
      />
    ),
    showVisibilityColumn && (
      <Column
        dataKey="visible"
        width={Spacing.ButtonColumnWidthPx}
        minWidth={Spacing.ButtonColumnWidthPx}
        cellRenderer={checkColumnAccess(
          { canMutate: 'updateWareCategory' },
          ColumnRendererVisibility,
        )}
        columnData={{ onToggle }}
      />
    ),
    showColor && (
      <Column
        dataKey="color"
        width={5}
        cellRenderer={({ rowData }) => (
          <ColorBadgeMenu color={rowData.color?.color} />
        )}
      />
    ),
    <Column
      dataKey="nameLabel"
      width={300}
      label={i18n.t`Name`}
      cellRenderer={ColumnRendererWithNote}
      columnData={{ noteKey: 'internalNote' }}
    />,
    <Column
      dataKey="countRecipes"
      label={i18n.t`Recipe count`}
      width={100}
      cellRenderer={({ cellData }) =>
        `${cellData} ${i18n.t`Recipes`.toLocaleLowerCase()}`
      }
    />,
    children,
    isSortable && (
      <Column dataKey="id" width={50} cellRenderer={() => <DragHandle />} />
    ),
  ]

  if (isSortable) {
    return React.createElement(
      // * Replacing this with Tabular will fix this
      // @ts-ignore
      SortableDataTable,
      {
        onSort: onSort as any,
        ...props,
      },
      ...columns,
    )
  }

  return React.createElement(DataTable, props, ...columns)
}
